@font-face {
  font-family: 'BebasNeue';
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/BebasNeueRegular.woff2") format("woff2");
  src: url("../fonts/BebasNeueRegular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: 'BebasNeue';
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/BebasNeueBold.woff2") format("woff2");
  src: url("../fonts/BebasNeueBold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: 'RobotoCondensed';
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2");
  src: url("../fonts/RobotoCondensed-Light.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: 'RobotoCondensed';
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: 'RobotoCondensed';
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  margin: 0;
  font-family: 'RobotoCondensed';
  font-style: normal;
  font-size: 16px;
  line-height: 140%; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 0;
  clip: rect(0 0 0 0); }

a {
  text-decoration: none; }

.container {
  padding: 0 4.8%;
  margin: 0 auto; }

@media (min-width: 768px) {
  .container {
    padding: 0 5.9%; } }

@media (min-width: 1200px) {
  .container {
    padding: 0 7.6%; } }

.header {
  font-family: 'BebasNeue';
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.02em; }
  .header a {
    color: white; }
  .header .container {
    position: absolute;
    display: flex;
    padding-top: 18px;
    width: 100%;
    justify-content: space-between; }

@media (min-width: 768px) {
  .header {
    font-size: 24px;
    line-height: 24px; }
    .header .container {
      padding-top: 38px; } }

@media (min-width: 1200px) {
  .header {
    font-size: 26px;
    line-height: 26px; }
    .header .container {
      padding-top: 28px; }
  .header__tel {
    letter-spacing: 0.04em; } }

.main {
  background-image: url("../img/main-bg-mobile.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  color: white; }
  .main a {
    color: white; }
  .main .container {
    display: flex;
    padding-top: 112px;
    padding-bottom: 78px; }

.main__text-wrapper {
  width: 100%; }

.main__header-wrapper {
  position: relative;
  margin-bottom: 42px;
  display: flex; }

.main__city {
  margin: 0;
  margin-bottom: 12px;
  text-align: right;
  font-family: 'BebasNeue';
  font-size: 28px;
  line-height: 28px;
  letter-spacing: 0.03em; }

.main__header {
  width: 290px;
  margin: 0;
  margin-left: auto;
  font-family: 'BebasNeue';
  font-size: 78px;
  line-height: 78px;
  text-align: right;
  letter-spacing: 0.03em;
  -webkit-text-stroke: 3px white; }

.main__gym-list {
  position: absolute;
  bottom: 12px;
  left: -1px;
  padding: 0;
  margin: 0;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
  list-style: none; }

.main__buy {
  display: block;
  padding: 21px 0 20px;
  width: calc(100% + 4px);
  margin-left: -2px;
  text-align: center;
  font-family: 'RobotoCondensed';
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 4px solid white; }

.main__img {
  display: none; }

.main__socials {
  display: none;
  list-style: none; }
  .main__socials li {
    font-family: 'RobotoCondensed';
    font-size: 12px;
    line-height: 14px;
    text-align: right;
    text-transform: uppercase; }

@media (min-width: 768px) {
  .main__img {
    display: block; }
  .main {
    background-image: url("../img/main-bg-tablet.png"); }
    .main .container {
      display: flex;
      justify-content: space-between;
      padding-top: 82px;
      padding-bottom: 82px; }
  .main__city {
    margin-bottom: 6px;
    font-size: 34px;
    line-height: 34px; }
  .main__header {
    font-size: 86px;
    line-height: 86px; }
  .main__text-wrapper {
    width: 354px;
    padding-top: 54px;
    padding-right: 30px; }
  .main__gym-list {
    bottom: 17px;
    left: 0; }
  .main__header-wrapper {
    margin-bottom: 40px; }
  .main__buy {
    width: 57.8%;
    margin-left: auto;
    margin-right: -2px;
    padding: 21px 0 18px; } }

@media (min-width: 1200px) {
  .main {
    background-image: url("../img/main-bg-desktop.png"); }
    .main .container {
      justify-content: unset;
      padding-top: 51px;
      padding-bottom: 43px; }
  .main__socials {
    margin-left: auto;
    padding-left: 0;
    padding-top: 418px;
    display: block; }
    .main__socials li {
      margin-bottom: 19px; }
  .main__city {
    margin-bottom: 13px;
    font-size: 49px;
    line-height: 49px; }
  .main__header {
    width: 300px;
    font-weight: bold;
    font-size: 109px;
    line-height: 109px; }
  .main__text-wrapper {
    margin-right: 2.6%;
    width: 425px;
    padding-top: 116px; }
  .main__header-wrapper {
    margin-bottom: 56px; }
  .main__buy {
    width: 59.5%; }
  .main__gym-list {
    bottom: 28px;
    left: 19px; }
  .main__img {
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); } }
